"use client";
import React from "react";
import Link from "next/link";
import { Inbox } from "lucide-react";

type Props = {
  className?: string;
  badge?: number | null;
  onClick?: () => void;
  ariaCurrent?: boolean;
};

export default function InboxLink({ className = "", badge = null, onClick, ariaCurrent }: Props) {
  return (
    <Link
      href="/dashboard/admin/inbox"
      onClick={onClick}
      className={`relative flex items-center gap-3 px-3 py-3 rounded-xl text-sm font-medium transition-all duration-200 ${className}`}
      aria-current={ariaCurrent ? "page" : undefined}
    >
      <div className="relative">
        <div className="w-9 h-9 rounded-lg flex items-center justify-center bg-slate-100 group-hover:bg-slate-200">
          <Inbox className="w-4 h-4 text-slate-600" />
        </div>
        {badge ? (
          <div className="absolute -top-1 -right-1 min-w-[18px] h-[18px] px-1 bg-gradient-to-br from-rose-500 to-pink-500 text-white text-[10px] font-bold rounded-full flex items-center justify-center shadow-lg">
            {badge}
          </div>
        ) : null}
      </div>

      <span className="flex-1 text-left">Inbox</span>
    </Link>
  );
}
