"use client";
import React from "react";
import Link from "next/link";
import { Users } from "lucide-react";

type Props = {
  className?: string;
  onClick?: () => void;
  ariaCurrent?: boolean;
};

export default function TeamsLink({ className = "", onClick, ariaCurrent }: Props) {
  return (
    <Link
      href="/dashboard/admin/teams"
      onClick={onClick}
      className={`relative flex items-center gap-3 px-3 py-3 rounded-xl text-sm font-medium transition-all duration-200 ${className}`}
      aria-current={ariaCurrent ? "page" : undefined}
    >
      <div className="w-9 h-9 rounded-lg flex items-center justify-center bg-slate-100 group-hover:bg-slate-200">
        <Users className="w-4 h-4 text-slate-600" />
      </div>
      <span className="flex-1 text-left">Teams</span>
    </Link>
  );
}
